.meal {
  display:flex;
  justify-content:space-between;
  border-bottom:1px solid #bbb8b8;
  margin:1rem;
  padding-bottom:1rem;
}
.meal h3 {
  margin:0 0 0.50rem 0;
}
.description {
  font-style:italic;
}
.price {
  color:var(--main-color);
  font-size:1.25rem;
  font-weight:bold;
  margin-top:0.50rem;
}
