.Backdrop {
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height:100vh;
  z-index:200;
  background:rgba(0,0,0,0.8);
}
.modal {
  background:#fff;
  position:fixed;
  top:20vh;
  left:5%;
  width:90%;
  padding:1rem;
  z-index:9999;
  border-radius:5px;
  box-shadow:2px 2px rgba(0,0,0,1);
}
@media (min-width:768px) {
  .modal {
    width:40rem;
    left:calc(50% - 20rem);
  }
}