.cart-items li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--main-color);
  padding:1rem 0;
}

.cart-items {
  margin-bottom:1rem;
}

.total {
  display:flex;
  justify-content:space-between;
  margin-bottom:1rem;
  font-size:1.5rem;
  font-weight:bold;
}

.action {
  text-align:right;
}

.action button {
  border-radius:10px;
  padding:5px 15px;
  margin-right:5px;
  cursor: pointer;
  background:#fff;
  border:2.5px solid rgba(33,33,33,0.7);
  font-size:0.9rem;
}
.action button:active {
  background:var(--accent-color);
  color:#fff;
  border-color:transparent;
}

.amount { 
  margin-left:2rem;
  border:1px solid #ccc;
  padding:0.3rem 0.8rem;
  border-radius: 0.5rem;
  color:#444444;
  font-weight: bold;
}


.cart-items .btns{
  display:flex;
  flex-direction:row;
}
.btns button {
  cursor: pointer;
  background: transparent;
  border:1px solid var(--main-color);
  border-radius:0.3rem;
  margin:0.3rem;
  color:var(--main-color);
  font-size:1.6rem;
  width:36px; height:36px;
}

.btns button:hover, .btns button:active {
  background: var(--main-color);
  color:white;
} 
