.form button {
  padding:3px 6px;
  background:var(--main-color);
  color:rgba(255,255,255,0.95);
  border:none;
  border-radius:5px;
  cursor:pointer;
  float:right;
  margin-top:8px;
}
.form button:hover {
  background:var(--accent-color);
  color:rgba(255,255,255,1);
}

.form button:active {
  
  filter:brightness(0.9);
}